import React, {FC} from "react";
import {Colleague} from "../App";
import {Card, Image} from "antd";

interface ColleagueCardProps {
    readonly mysteryColleague: Colleague;
}
const ColleagueCard: FC<ColleagueCardProps> = ({mysteryColleague}) => {
    return(
        <div style={{ marginTop: "16px" }}>
            <Card>
                {mysteryColleague.Name}
                <Image preview={false} src={"https://flagsapi.com/" + mysteryColleague.State + "/flat/32.png"}></Image>
            </Card>
        </div>)

}

export default ColleagueCard;